
.hero-section {
    width: 1320px;
    height: 530px;
    top: 159px;
    background-repeat: no-repeat;
    background-size: 648px 530px;
    background-position: 50% 50%;
    margin-top: 110px;
    position: relative;
    left: 50%;
    transform: translate(-50%, 0);
}

.hero-section__title{
    font-family: Playfair Display;
    font-size: 50px;
    font-weight: 400;
    line-height: 75px;
    text-align: left;
    text-transform: uppercase;
    align-items: left;
    position: absolute;
    top: -200px;
}

.hero-section__images{
    display: flex;
    flex-direction: row;
    align-items: center;
    position: relative;
}

.img1 {
    
    padding-left: 0;
    padding-bottom: 0;
    width: 310px;
    height: 329px;
}

.img2 {
    width: 648px;
    position: absolute;
    bottom: 0px;
    left: 330px;
    z-index: -100;
}

.img3 {
    padding-left: 698px;
    padding-bottom: 0px;
    height: 337px;
    position: absolute;
    top: -200px;
    left: 300px;
}

.hero-section__div {
    position: absolute;
    left: 1000px;
    top: 195px;
}

.hero-section__text {
    font-family: Roboto;
    font-size: 18px;
    font-weight: 400;
    line-height: 27px;
    text-align: left;
    padding-bottom: 30px;
}

.hero-section__btn {
    font-family: Roboto;
    font-size: 18px;
    font-weight: 400;
    line-height: 24px;
    text-align: left;
    width: 224px;
    height: 50px;
    padding: 17px 65px 17px 65px;
    gap: 6px;
    opacity: 0px;
    color: white;
    background-color: #0A224A;
    cursor: pointer;
    border: none;
}

